@import "mixins";
@import "fonts";
@import "vendor";
@import "vars";
@import "settings";

/*===========projects style==========*/

body {
	font-family: 'Montserrat', serif;
	font-size: 14px;
	line-height: 24px;
	color: var(--dark-Shade75);
	background-color: #fff;
  }
  
  h1 {
	font-family: 'Montserrat';
	font-size: 24px;
	color: var(--dark-Shade100);
  }
  
  h2 {
	font-family: 'Montserrat';
	font-size: 19px;
	line-height: 23px;
	color: var(--dark-Shade100);
  }
  
  h3 {
	font-family: 'Montserrat';
	font-size: 12px;
	line-height: 15px;
	text-transform: uppercase;
	color: var(--dark-Shade100);
  }
  
  .container {
	margin: 0 auto;
	padding: 0 10px;
	max-width: 1180px;
  }
  
  /*===========header==========*/
  
  .header-container {
	padding-top: 15px;
	padding-bottom: 15px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
		-ms-flex-align: center;
			align-items: center;
	z-index: 100;
  }
  
  .header__logo {
	margin-right: 285.31px;
	width: 105.69px;
	height: 40px;
	background: url(../img/svg/Logo.svg) no-repeat;
	background-position: center;
	background-size: cover;
	z-index: 100;
  }
  
  .header__nav {
	margin-right: 25px;
  }
  
  .header__nav .header-nav__list {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
  }
  
  .header-nav__item:not(:last-child) {
	margin-right: 23px;
  }
  
  .header_nav_item_drop {
	padding-right: 17px;
	position: relative;
  }
  
  .header_nav_item_drop::before {
	content: "";
	position: absolute;
	width: 10px;
	height: 10px;
	right: 0;
	top: 5px;
	cursor: pointer;
	-webkit-transform: rotate(-45deg);
		-ms-transform: rotate(-45deg);
			transform: rotate(-45deg);
	border-left: 2px solid var(--dark-Shade50);
	border-bottom: 2px solid var(--dark-Shade50);
  }
  
  .login__btn {
	margin-right: 20px;
	width: 87px;
	height: 34px;
	border: 2px solid #8ba4f9;
	border-radius: 22px;
	font-size: 12px;
	line-height: 15px;
	font-weight: 700;
	text-transform: uppercase;
	color: var(--purple-color);
	cursor: pointer;
  }
  
  .reg__btn {
	width: 196px;
	height: 34px;
	background: -webkit-gradient(linear, left top, left bottom, from(#BC9CFF), to(#8BA4F9));
	background: -o-linear-gradient(top, #BC9CFF 0%, #8BA4F9 100%);
	background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
	border-radius: 22px;
	font-size: 12px;
	line-height: 15px;
	font-weight: 700;
	text-transform: uppercase;
	color: #FFFFFF;
	cursor: pointer;
  }
  
  .burger {
	display: none;
  }
  
  .burger span {
	position: absolute;
	top: 9px;
	right: 0;
	width: 20px;
	height: 3px;
	background: var(--purple-color);
  }
  
  .burger.active span {
	opacity: 0;
  }
  
  /*==============footer=============*/
  
  .hero {
	height: 831px;
	background: url(../img/HeroBanner.jpg) no-repeat;
	background-position: center bottom 2px;
	background-size: cover;
  }
  
  .hero-container {
	padding: 70px 10px 20px 10px;
  }
  
  .hero__selection {
	margin-bottom: 317px;
	padding: 40px 30px 30px;
	width: 380px;
	background: #FFFFFF;
	border: 1px solid rgba(0, 0, 0, 0.12);
	-webkit-box-sizing: border-box;
			box-sizing: border-box;
	-webkit-box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
			box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
	border-radius: 4px;
  }
  
  .hero-selection__title {
	margin-bottom: 21px;
	line-height: 28px;
  }
  
  .input__name {
	margin-bottom: 6px;
  }
  
  .hero-selection__form {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
		flex-wrap: wrap;
  }
  
  .selection__lable {
	display: inline;
  }
  
  .selection__lable:first-child {
	margin-right: 18px;
  }
  
  .selection__date {
	margin-bottom: 22px;
	padding-left: 10px;
	width: 150px;
	height: 44px;
	background: #FFFFFF;
	border: 1px solid var(--dark-Shade25);
	border-radius: 4px;
  }
  
  .selection__guest {
	margin-bottom: 30px;
	width: 320px;
	height: 44px;
	background: #FFFFFF;
	border: 1px solid var(--dark-Shade25);
	border-radius: 4px;
  }
  
  .selection__btn {
	width: 320px;
	height: 44px;
	background: -webkit-gradient(linear, left top, left bottom, from(#BC9CFF), to(#8BA4F9));
	background: -o-linear-gradient(top, #BC9CFF 0%, #8BA4F9 100%);
	background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
	border-radius: 22px;
	cursor: pointer;
	font-size: 12px;
	line-height: 15px;
	font-weight: 700;
	text-align: center;
	text-transform: uppercase;
	color: #fff;
  }
  
  .hero__description {
	margin-left: auto;
	display: block;
	text-align: end;
	width: 297px;
	height: 48px;
  }
  
  @media (max-width: 1440px) {
	.hero {
	  margin: 0 auto;
	  max-width: 1440px;
	}
  }
  
  @media (max-width: 1190px) {
	.container {
	  max-width: 1024px;
	}
  
	.header__logo {
	  margin-right: 100px;
	}
  }
  
  @media (max-width: 1024px) {
	.header__nav .header-nav__list {
	  padding: 70px 10px;
	  position: fixed;
	  left: 0;
	  top: 0;
	  right: 0;
	  bottom: 0;
	  opacity: 0;
	  visibility: hidden;
	  background: #fff;
	  display: -webkit-box;
	  display: -ms-flexbox;
	  display: flex;
	  -webkit-box-orient: vertical;
	  -webkit-box-direction: normal;
		  -ms-flex-direction: column;
			  flex-direction: column;
	  font-size: 24px;
	  line-height: 36px;
	  color: var(--dark-Shade75);
	  -webkit-transition: all 0.3s linear;
	  -o-transition: all 0.3s linear;
	  transition: all 0.3s linear;
	}
  
	.header-nav__list.active {
	  opacity: 1;
	  visibility: visible;
	}
  
	.header_nav_item_drop {
	  padding: 0;
	}
  
	.header_nav_item_drop::before {
	  display: none;
	}
  
	.login__btn {
	  display: none;
	}
  
	.reg__btn {
	  display: none;
	}
  
	.burger {
	  position: relative;
	  margin-left: auto;
	  width: 35px;
	  height: 20px;
	  display: block;
	  -webkit-transition: all 0.3s linear;
	  -o-transition: all 0.3s linear;
	  transition: all 0.3s linear;
	}
  
	.burger::before,
	.burger::after {
	  content: '';
	  position: absolute;
	  width: 35px;
	  height: 3px;
	  top: 0;
	  left: 0;
	  background: var(--purple-color);
	  -webkit-transition: all 0.3s linear;
	  -o-transition: all 0.3s linear;
	  transition: all 0.3s linear;
	}
  
	.burger::after {
	  top: 18px;
	}
  
	.burger.active {
	  width: 40px;
	  height: 40px;
	  -webkit-animation: rotate 0.3s ease-in-out;
			  animation: rotate 0.3s ease-in-out;
	}
  
	.burger.active::before,
	.burger.active::after {
	  content: '';
	  top: 15px;
	  left: 3px;
	  width: 28px;
	  background: var(--purple-color);
	}
  
	.burger.active::before {
	  -webkit-transform: rotate(45deg);
		  -ms-transform: rotate(45deg);
			  transform: rotate(45deg);
	}
  
	.burger.active::after {
	  -webkit-transform: rotate(-45deg);
		  -ms-transform: rotate(-45deg);
			  transform: rotate(-45deg);
	}
  }
  
  @media (max-width: 403px) {
	.hero__selection {
	  width: 100%;
	}
  
	.selection__lable {
	  width: 100%;
	}
  
	.selection__lable:first-child {
	  margin-right: 0;
	}
  
	.selection__date {
	  width: 100%;
	}
  
	.selection__guest {
	  width: 100%;
	}
  
	.selection__btn {
	  width: 100%;
	}
  }
