*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

ul[class],
ol[class] {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

body {
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
}

ul[class],
ol[class] {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  vertical-align: bottom;
}

input,
button,
textarea,
select {
  font: inherit;
}

button {
  border: none;
  background-color: transparent;
}

*:focus {
  outline: none;
}

/*===========var==========*/

:root {
  --dark-Shade100: #1F2041;
  --dark-Shade75: rgba(31, 32, 65, 0.75);
  --dark-Shade50: rgba(31, 32, 65, 0.5);
  --dark-Shade25: rgba(31, 32, 65, 0.25);
  --dark-Shade5: rgba(31, 32, 65, 0.05);
  --purple-color: #BC9CFF;
  --green-color: #6FCF97;
}
